#navbar {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 10px 25px;
  margin-bottom: 15px;
}

#navbar a {
  color: #000;
  text-decoration: none;
  font-size: 1.25em;
  margin-left: 10px;
}

#navbar a:hover {
  font-weight: bold;
}